// @flow
import * as React from 'react';
import { ShareButtons } from './ShareButtons';
const QRCode = require('qrcode.react');

type Props = {
  qrCodeValue: string;
};
export const QRCodeContainer = (props: Props) => {
  if (props.qrCodeValue) {
    return (
      <div className="self-center flex justify-center flex-col">
        <QRCode size={256} value={props.qrCodeValue} />
        <ShareButtons />
      </div>
    );
  }
  return null;
};
